*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
.wrapper{
    width: 90%;
    margin-left: 65px;
}

@media (max-width: 599px) { 
  .wrapper{
    width: 100%;
    margin-left: 0;
    padding: 1rem 1rem;
  }
}
